import {Injectable} from '@angular/core';
import {Class} from './interfaces';
import {HttpClient} from '@angular/common/http';
import {Observable} from 'rxjs';
import {tap} from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})

export class ClassService {

  // private mainclesses = null;

  constructor(private http: HttpClient ) {
  }

  getMain(): Observable<Class[]> {
    return this.http.get<Class[]>(`/api/class`);
  }
  getChildren(id: string): Observable<Class[]> {
    return this.http.get<Class[]>(`/api/class/children/${id}`);
  }
  getById(id: string): Observable<Class> {
    return this.http.get<Class>(`/api/class/${id}`);
  }
  getUp(id: string): Observable<Class[]> {
    return this.http.get<Class[]>(`/api/class/up/${id}`);
  }
}
