import {Injectable} from '@angular/core';
import {Application, NewUser} from './interfaces';
import {HttpClient} from '@angular/common/http';
import {Observable} from 'rxjs';


@Injectable({
  providedIn: 'root'
})


export class AccountService {

  constructor(private http: HttpClient ) {
  }

  registerApplication(application: Application): Observable<Application> {
    console.log('posting app', application)
    return this.http.post<Application>('/api/account', application);
  }

}
