import {Injectable} from '@angular/core';
import {NewUser, User} from './interfaces';
import {HttpClient} from '@angular/common/http';
import {Observable} from 'rxjs';
import {tap} from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class AuthService {

  private token = null;
  private userName = null;

  constructor(private http: HttpClient ) {
  }
  register(user: NewUser): Observable<NewUser> {
    return this.http.post<NewUser>('/api/auth/register', user);
  }

  login(user: User): Observable<{token: string, name: string}> {
    return this.http.post<{token: string, name: string}>('/api/auth/login', user)
      .pipe(
        tap(({token, name}) => {
          localStorage.setItem('auth-token', token);
          localStorage.setItem('username', name);
          this.setToken(token);
          this.setName(name);
        })
      );
  }

  setToken(token: string) {
    this.token = token;
  }
  getToken(): string {
    return this.token;
  }
  setName(name: string) {
    this.userName = name;
  }
  getName(): string {
    return this.userName;
  }
  isAuthenticated(): boolean {
    return !!this.token;
  }
  logout() {
    this.setToken(null);
    this.setName(null);
    localStorage.clear();
  }

}
